/* App.css */

body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #1e1e1e; /* Dark background color */
  color: #fff; /* Text color */
  background-image: url('../public/wall.jpg');
  background-repeat: repeat; /* Изображение будет повторяться как плитка */
  background-size: cover; /* Заполнит всю область заданного фона */
  /* или */
  background-size: 50%; /* Оставит оригинальный размер изображения и будет центрировать его в заданной области */
}

.App {
  text-align: center;
  padding: 20px;
  margin-top: 0;
  padding-top: 10px;
}

.title {
  margin-top: 0;
  padding-top: 0;
  font-size: 2em;
  margin-bottom: 20px;
}

input {
  padding: 8px;
  font-size: 16px;
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  border-radius: 12px; 
  overflow: hidden; 
  background-color: #1e1e1e;
}

thead {
  background-color: #333; /* Dark header background color */
}

td {
  padding: 10px;
  text-align: center;
  border: 1px solid #555; /* Dark border color */
}

/* Hover effect on table rows */
tbody tr:hover {
  background-color: #444; /* Dark hover background color */
}

/* Styling for positive Change % */
td.change-positive {
  color: #00ff00; /* Green color for positive change */
}

/* Styling for negative Change % */
td.change-negative {
  color: #ff0000; /* Red color for negative change */
}

.button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border: 2px solid #333; /* Цвет рамки */
  color: #fff; /* Цвет текста */
  background-color: #333; /* Основной цвет фона */
  border-radius: 5px; /* Скругление углов */
  transition: background-color 0.3s, color 0.3s; /* Плавные анимации перехода */
}

/* Состояние при наведении */
.button:hover {
  background-color: #555; /* Цвет фона при наведении */
  color: #fff; /* Цвет текста при наведении */
}

/* Состояние при активации (нажатии) */
.button:active {
  background-color: #000; /* Цвет фона при активации */
  color: #fff; /* Цвет текста при активации */
}

.logopic {
  width: 3%;
  height: 3%;
  padding: 0;
  margin: 0;
}

.footer {
  margin:0;
  padding: 0;
}
